// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		news: []
	},
	mutations: {
		getNews (state, news){
			state.news = news.news
		}
	},
	actions: {
		getNews (context, cat){
			fetch('/api/getNews?cat='+ cat.name).then(function(data){ console.log(data.json());context.commit('getNews', {'news': data.json()['data']})})
		}
	}
})

new Vue({
  el: '#app',
  store,
  template: '<App/>',
  components: { App }
})



