<template>
  <div id="app">
    <h1>Vue News</h1>
    <ul>
      <li v-for='n in news'>{{n.title}}</li>
    </ul>
    <button @click='change'>click</button>
  </div>
</template>

<script>
export default {
  name: 'app',
  methods: {
    change () {
      this.$store.dispatch('getNews', {'name': 'hacker'})
    }
  },
  computed: {
    news () {
      return this.$store.news
    }
  }
}
</script>

